import { useUser } from "@clerk/clerk-react"
import { CqsItem, Watchlist } from "@rp/common/api-types"
import { defaultWatchlistImage } from "@rp/react/common/assets"
import { getWatchlistOwnerInfo, usePerformanceIndicator } from "@rp/react/common/watchlists"
import { Box } from "@rp/react/web/mui/core"
import { useWatchlistsActions } from "../hooks/useWatchlistsActions"
import { WatchlistActions } from "./WatchlistActions"
import { WatchlistDescription } from "./WatchlistDescription"
import { WatchlistDetails } from "./WatchlistDetails"
import { WatchlistImage } from "./WatchlistImage"
import { WatchlistName } from "./WatchlistName"

type Props = {
  canEdit: boolean
  watchlist: Watchlist
  entities?: Array<CqsItem> | undefined
  openUploadModal?: () => void
}

const WatchlistPortfolioHeader = ({ canEdit, watchlist, entities, openUploadModal }: Props) => {
  const { average } = usePerformanceIndicator(watchlist.id)
  const { rename } = useWatchlistsActions()
  const { user } = useUser()
  const { avatar, displayName, name } = getWatchlistOwnerInfo(watchlist, user)
  const enableBrief = entities?.some(item => item.category === "Companies") ?? false

  return (
    <>
      <Box sx={{ display: "flex", width: "100%", gap: 1.5 }}>
        <WatchlistImage
          canEdit={canEdit}
          rename={rename}
          watchlist={watchlist}
          url={watchlist.imageUrl ?? defaultWatchlistImage}
        />
        <Box sx={{ display: "flex", flexDirection: "column", gap: 1, flex: 1, overflow: "hidden" }}>
          <WatchlistName canEdit={canEdit} watchlist={watchlist} rename={rename} />
          <WatchlistDescription canEdit={canEdit} watchlist={watchlist} rename={rename} />
          <WatchlistDetails
            watchlist={watchlist}
            ownerName={name}
            displayName={displayName}
            averageChangePercentage={average}
            imageUrl={avatar}
          />
        </Box>
      </Box>
      <WatchlistActions
        enableBrief={enableBrief}
        watchlist={watchlist}
        editable={canEdit}
        openUploadModal={openUploadModal}
      />
    </>
  )
}

export { WatchlistPortfolioHeader }
